<template>
  <div>
    <div class="back" @click="back"> < 返回</div>
    <h3 class="news-title">{{this.$route.params.news[getIndex].title}}</h3>

    <p class="news-content" v-html="content"></p>

    <div  class="page" @click="last" v-if="getIndex>0">上一个新闻：{{this.$route.params.news[getIndex-1].title}}</div>
    <div  class="page" v-else>没有了</div>
    <div class="page" @click="next" v-if="getIndex<this.$route.params.news.length-1">下一个新闻：{{this.$route.params.news[getIndex+1].title}}</div>
    <div  class="page" v-else>没有了</div>


  </div>
</template>
<script>
  export default {
    data(){
      return{
        getIndex:this.$route.params.index,
        content:this.$route.params.news[this.$route.params.index].text,
      }
    },
    methods:{
      back(){
        this.$router.go(-1)
      },
      last(){
        if(this.getIndex>0){
          this.getIndex--
          this.content=this.$route.params.news[this.getIndex].text
        }
      },
      next(){
        if(this.getIndex<this.$route.params.news.length-1){
          this.getIndex++
          this.content=this.$route.params.news[this.getIndex].text
        }
      }
    }
  }
</script>
<style>
  .back{
    width: 100px;
    background: #eeeeee;
    color: #333333;
    font-size: 20px;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
  }
  .page{ margin: 20px}
  h3.news-title{ font-size: 18px; margin: 12px;}
  span.news-time{ font-size: 12px; color: #cccccc;}
  p.news-content{ text-align: left; padding: 14px;}
</style>



