<template>
  <div class="foot_nav">
     <router-link :to="{path:'/Form'}"><span><img src="../assets/images_home/tab_iocn_62.png">注册试学</span></router-link>
      <span @click="tel"><img src="../assets/images_home/tab_iocn_64.png">联系我们</span>
      <span><img src="../assets/images_home/tab_iocn_66.png">我的设置</span>
  </div>
</template>
<script>
import '../assets/foot.css'
import { MessageBox } from 'mint-ui';
  export default {
  data(){
    return {
      num: '10086'
    }
    },
      methods:{
      tel(){
        MessageBox({
          title: '正保外语教育',
          message:'<div style="color:red">'+this.num+'</div>',
          confirmButtonText:'拨打电话'
        }).then(action => {
          window.location.href='tel://'+this.num
        });
    }
  }
  }
</script>
<style>

</style>



