import Vue from 'vue'
import Router from 'vue-router'
import About from '../pages/about'
import Course from '../pages/course'
import Home from '../pages/home'
import Job from '../pages/job'
import Ours from '../pages/ours'
import Teacher from '../pages/teacher'
import FormSub from '../pages/Form'
import News from '../pages/news'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Home
    },
    {
      path:'/home',
      component:Home
    },
    {
      path:'/job',
      component:Job
    },
    {
      path:'/about',
      component:About
    },
    {
      path:'/course',
      component:Course
    },
    {
      path:'/ours',
      component:Ours
    },
    {
      path:'/teacher',
      component:Teacher
    },
    {
      path: '/Form',
      component: FormSub
    },
    {
      name: 'News',
      path: '/news',//分配的路由地址
      component:News
    }
  ]
})



